.draftBtn {
  box-sizing: border-box;
  min-width: 160px;
  width: 100%;
  height: 48px;
  display: flex;
  align-items: center;
  line-height: 20px;
  color: #1e2528;
  justify-content: center;
  border: 1.5px solid #1e2528;
  border-radius: 10px;
  background-color: #ffffff;
  font-size: 14px;
  font-weight: 500;
  padding: 0 16px;
}

.applyBtn {
  // box-sizing: border-box;
  width: 100%;
  height: 48px;
  display: flex;
  align-items: center;
  line-height: 20px;
  color: white;
  justify-content: center;
  font-size: 14px;
  font-weight: 500;
  padding: 0 16px;
  border-radius: 8px;
  background-color: #1E2528;
  border: 1px solid #D2D3D4;

  &:disabled {
    background: #D2D3D4;
  }
}

.submitBtn {
  box-sizing: border-box;
  width: 100%;
  height: 48px;
  border: none;
  display: flex;
  align-items: center;
  line-height: 20px;
  color: #ffffff;
  justify-content: center;
  border-radius: 10px;
  background-color: #1e2528;
  font-size: 14px;
  font-weight: 500;
  padding: 0 16px;
  min-width: 160px;
}

.applyStructure {
  font-family: "Barlow";
  padding: 8px 16px;
  border-radius: 8px;
  height: 38px;

  &:disabled {
    opacity: 1;
    background-color: #D2D3D4 !important;
  }
}

.resetBtn {
  box-sizing: border-box;
  width: 100%;
  height: 38px;
  border: none;
  display: flex;
  align-items: center;
  line-height: 20px;
  color: #1E2528;
  justify-content: center;
  border-radius: 8px;
  background-color: #fff;
  font-size: 14px;
  font-weight: 500;
  padding: 8px 16px;
  border: 1px solid #1E2528;
}

.cancelBtn {
  box-sizing: border-box;
